<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process133"></a>

<p><br></p>
<p><span class="rvts17">布拉克斯·雷克斯藏宝室（</span><span class="rvts50">The Vault of Braccus Rex</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span></p>
<p class="rvps2"><span class="rvts41">任务坐标：</span>
<br><span class="rvts20">X:514,Y:73</span><span class="rvts21">，地点：黑暗峡谷洞口</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">探索黑暗峡谷</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1060.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">可以在一处海滩处（遭遇</span><span class="rvts20">3</span><span class="rvts21">只变异鳄鱼后），遇到一处隐蔽的山洞。这里就是黑暗峡谷。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1061.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">之后会遇到一个处于不稳定状态的川普多依（</span><span class="rvts20">Trompdoy</span><span class="rvts21">），继续探索洞穴，会和他以及他的幻象进行战斗。</span>
<br><span class="rvts21">战斗过程中，凡是被攻击后变为蓝色空洞状态的敌人，都是普多依的幻想，另外，使用下雨或血雨，都会使他的隐身状态失效。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1062.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">注意该山洞中，有很多地方都有隐藏的桥梁。仔细观察小地图，别被眼前的空无而迷惑。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1063.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">在进入大厅后，有一尊雕像，他会问你两个问题。需要全部回答正确，真正的藏宝密室门才会打开。之后进入密室，毁掉川普多依的灵柩罐，并会完成该任务。</span>
<br><span class="rvts21">两个问题的正确答案分别是选择第一个和第二个。（如果懂得英语，那么原版的解密十分简单，雕像所说的答案就是每个单词的首字母，另外进门处的老鼠会提示你这一点。）</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



